import { AfterViewInit, Component, OnInit, ViewChild } from '@angular/core';
import {
  FormGroup,
  FormBuilder,
  FormControl,
  Validators, NgForm
} from "@angular/forms";
import { Subject } from 'rxjs';
import { map } from 'rxjs/operators';
import { HomeService } from "src/app/service/home.service";

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.css']
})
export class ContactComponent implements OnInit, AfterViewInit {
  @ViewChild('frm') frm:NgForm;
  text: string = '';
  catForm: FormGroup;
  constructor(private fb: FormBuilder, private imgservice: HomeService) {
    this.catForm = this.fb.group({
      firstname: new FormControl("", [Validators.required]),
      lastname: new FormControl("", [Validators.required]),
      Email:new FormControl("", [Validators.required,Validators.email]),
      subject:new FormControl("", [Validators.required]),
      description:new FormControl("", [Validators.required, Validators.maxLength(350)]),
    });
   }
descount
descnt
a
box:string = '';
  ngAfterViewInit():void{
    const formvalue = this.frm.valueChanges;
    formvalue.subscribe(res =>{
      this.descount=res
      this.descnt=res.description
      this.a= this.descnt;
      console.log(this.a)
    })
  }
  
  ngOnInit(): void {
    this.newcatForm()
  }

  newcatForm(){
    if(this.catForm.valid){
      this.imgservice.uploadContactUs(this.catForm.value).subscribe(res =>{
        console.log(res)
        this.catForm.reset();
        this.box = '';
        this.text = '';
      })
    }
  }

  addContactUs() {
    let firstname = this.catForm.controls.firstname.value;
    let lastname = this.catForm.controls.lastname.value;
    let Email = this.catForm.controls.Email.value;
    let subject = this.catForm.controls.subject.value;

    let formData = new FormData();
    formData.append("fname", firstname);
    formData.append("lname", lastname);
    formData.append("email", Email);
    formData.append("subject", subject);

    console.log(formData);
    this.imgservice.uploadContactUs(formData).subscribe((res) => {
      console.log(res);
    });
  }

}
