import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { Observable } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';
import { HttpClient } from "@angular/common/http";

@Component({
  selector: 'app-fashion',
  templateUrl: './fashion.component.html',
  styleUrls: ['./fashion.component.css']
})
export class FashionComponent implements OnInit {
 
  gdata
  public url="http://localhost:8080/"
  //alldata;
  //portrait:Observable<any>;
  portrait
  allProducts:Observable<any>;
  constructor(private http: HttpClient) { }

  ngOnInit(): void {
    this.allProducts = this.http.get(this.url+'server/getuploadImg').pipe(
      shareReplay()
    );
    this.portrait = this.allProducts.pipe(
      shareReplay(),
      map(res =>{
        this.gdata= res.data.filter(portraitdata =>{return portraitdata.catgry=="fashion"})
         console.log(this.gdata)
        console.log(this.portrait)
      })
    ).subscribe()
    
  }

  @ViewChild('h1')
  h1: ElementRef;

  onClick(): void {
    this.h1.nativeElement.scrollIntoView({behavior: 'smooth'}); 
  }

  fullscreen(){
    document.querySelector("body").requestFullscreen({ navigationUI: "auto" })
  }
}

